@import '../../../../../styles/mixins.scss';

.vaultItemContainer {
  display: flex;

  @include themify() {
    color: themed('textPrimary');
  }

  text-transform: initial;
  font-size: 14px;
  font-weight: 500;

  &>div {
    &>div {
      display: flex;
    }
  }

  .imgContainer {
    display: flex;
    margin-top: 0;
    margin-left: 0rem;
    margin-right: 12px;

    @media (max-width: 1199px) {
      margin-right: 4px;
    }

    img {
      width: 36px;
      height: 36px;
      display: block;
      filter: drop-shadow(2px 0px 2px rgba(0, 0, 0, 0.199301));
    }

    .imgClass {
      img {
        width: 36px;
        height: 36px;
      }

      @media (max-width: 1199px) {
        flex-direction: row;
      }
    }
  }

  p {
    margin: 0;
  }

  .protocols {
    text-transform: capitalize;
    align-items: center;

    // @include btn();
    margin: 0;

    img {
      width: 16px;
      height: 16px;
      padding-right: 4px;
      filter: drop-shadow(2px 0px 2px rgba(0, 0, 0, 0.199301));
    }
  }
}