@use 'sass:map';
@import '../../../../../styles/mixins.scss';

.graph-tooltip {
  border-radius: 10px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  gap: 10px;


  >&.mobile,
  >&.mini {
    border-radius: 2px;
    padding: 4px 8px;
    gap: 4px;
  }

  .tooltip-float-right {
    float: right;
    margin-left: 12px;

    &.mobile {
      margin-left: 4px;
    }
  }

}